import { TouchableOpacity, View, FlatList, StyleSheet } from "react-native";
import Icon from 'react-native-vector-icons/FontAwesome';
import { createCommonStyles } from "../styles";
import { Text, useColorScheme } from "@blacktokki/core";

export interface NodeData {
    path: string;
    title: string;
    level: number;
    header: string;
    description: string;
  }
  
export function parseHtmlToSections(html: string): NodeData[] {
    const parser = new DOMParser();
    const doc = parser.parseFromString(html, "text/html");
  
    const result: NodeData[] = [{path:"", title:"", header:"", level:0, description:""}];
  
    const headings: string[] = ["H1", "H2", "H3", "H4", "H5", "H6"];
    const headerStack: { level: number; title: string }[] = [];
  
    let current: NodeData | null = null;
    let cursor = doc.body.firstChild;
  
    const flushCurrent = () => {
      if (current) {
        result.push(current);
        current = null;
      }
    };
  
    while (cursor) {
      if (cursor.nodeType === Node.ELEMENT_NODE) {
        const el = cursor as HTMLElement;
        if (headings.includes(el.tagName)) {
          flushCurrent();
  
          const level = parseInt(el.tagName.substring(1));
          const title = el.textContent?.trim() || "";
  
          // 헤더 스택 업데이트
          while (headerStack.length > 0 && headerStack[headerStack.length - 1].level >= level) {
            headerStack.pop();
          }
          headerStack.push({ level, title });
  
          const path = headerStack.map(h => h.title).join(" > ");
  
          current = {
            path,
            title,
            level,
            header: el.outerHTML,
            description: "",
          };
        } else if (current) {
          current.description += el.outerHTML;
        } else {
          // 헤더 밖의 내용 처리
          result[0].description += el.outerHTML;
        }
      } else if (cursor.nodeType === Node.TEXT_NODE && current) {
        current.description += cursor.textContent || "";
      }
  
      cursor = cursor.nextSibling;
    }
  
    flushCurrent();
    return result;
}

export default function HeaderSelectBar(props:{root:string, path:string, onPress:(item:NodeData)=>void, data:NodeData[]}){
  const theme = useColorScheme();
  const commonStyles = createCommonStyles(theme);
  const renderItem = (item:NodeData) => <TouchableOpacity
    style={styles.resultItem}
    onPress={() => props.onPress(item)}
  >
    {item.level===0 && <Icon name="file-text-o" size={18} color="#FFFFFF"/>}
    <Text style={[props.path===item.path?[commonStyles.title, {marginBottom:5, marginTop:3}]:commonStyles.text, styles.resultText, {left:item.level * 10 + 10}]}>{item.level===0?props.root:item.title}</Text>
  </TouchableOpacity>
  return <View style={[styles.resultsContainer, theme === 'dark' ? styles.darkResults : styles.lightResults]}>
    <FlatList
      data={props.data}
      keyExtractor={(item) => item.path}
      renderItem={({ item }) => renderItem(item)}
      ItemSeparatorComponent={() => <View style={commonStyles.resultSeparator} />}
    />
  </View>
}


const styles = StyleSheet.create({
  backButton: {
    padding: 8,
  },
  resultItem: {
    padding: 10,
    flexDirection:'row',
  },
  resultText: {
    fontSize: 14,
  },
  buttonContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  resultsContainer: {
    borderWidth: 1,
    borderRadius: 4,
  },
  lightResults: {
    backgroundColor: '#FFFFFF',
    borderColor: '#CCCCCC',
  },
  darkResults: {
    backgroundColor: '#222222',
    borderColor: '#444444',
  },
})